<template>
    <div id="template">
        <v-btn  id="print" class="blue white--text mr-4 mb-5" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="red white--text mb-5" to="/cetaknotanonppn" link>Back</v-btn>
        <p id="print">Catatan : <br> *Kertas Print = 155 x 250 mm <br> *Orientasi = Portrait</p>
        <template id="template">
            <div v-for="i in karton" :key="i.index" class="grid-container"  >
                <template>
                    <div class="grid-item">
                        <table>
                            <tr>
                                <td></td>
                                <td></td>
                                <td style="font-size:10pt">Surakarta, ___________________________ <br>
                                    Kepada Yth.
                                </td>
                            </tr>
                            <!-- <tr>
                                <td></td>
                                <td></td>
                                <td></td>
                            </tr> -->
                            <tr>
                                <td style="font-size:16pt;width:6cm;padding-left:0.5cm;"><b>NOTA</b></td>
                                <td  style="font-size:16pt; width:6cm;"><b>{{ i["No Nota"]}}</b></td>
                                <td style="width:8cm;font-size:10pt;"> {{ i["Nama Pelanggan"]}} <br> {{ i["Alamat Pelanggan"]}} <br> <u>{{ i["Kota Pelanggan"]}}</u></td>
                            </tr>
                            <tr>
                                <td></td>
                                <td></td>
                                <td></td>
                            </tr>
                            <tr>
                                <td></td>
                                <td></td>
                                <td  style="text-align:center;font-size:10pt">{{ i["Barcd"] }}</td>
                            </tr>
                            <tr>
                                <td style="font-size:10pt">Kondisi Pembayaran,________________</td>
                                <td style="font-size:10pt">___________________________</td>
                                <td></td>
                            </tr>
                            
                        </table>
                        <table id="tabel" style="width: 20cm;">
                           <tr>
                               <th id="th" style="font-size:10pt">Quantity</th>
                               <th id="th" style="font-size:10pt">Nama Barang</th>
                               <th id="th" style="font-size:10pt">Harga (Rp) </th>
                               <th id="th" style="font-size:10pt">Jumlah Rupiah (Rp)</th>
                           </tr>
                           <tr style="font-size:10pt" v-for="j in kartond" :key="j.index">
                               <td id="td" style="text-align:center;" v-if="i['No Nota'] === j['No Nota']"> {{ j["Quantity"] | currency}}</td>
                               <td id="td" style="padding-left:0.1cm;" v-if="i['No Nota'] === j['No Nota']"> {{ j["Nama Barang"] }}</td>
                               <td id="td" style="text-align:right;padding-right:0.1cm;" v-if="i['No Nota'] === j['No Nota']">{{ j["Harga"] | currency }}</td>
                               <td id="td" style="text-align:right;padding-right:0.1cm;" v-if="i['No Nota'] === j['No Nota']">{{ j["Harga"] * j["Quantity"] | currency}}</td>
                           </tr>
                        </table><br>
                        <table id="tabel" style="width: 20cm;">
                           <tr style="font-size:10pt">
                               <td id="td" rowspan="3" style="width:12cm;vertical-align:top;padding-left:0.1cm">Terbilang <br> <i> {{ i["terbilang"]}} </i></td>
                               <td id="td" style="padding-left:0.1cm;">Jumlah Kotor</td>
                               <td id="td" style="width:4cm;text-align:right;padding-right:0.1cm;">{{ i["Jumlah Kotor"] | currency }}</td>
                           </tr>
                           <tr style="font-size:10pt">
                               <td id="td" style="width:4cm;padding-left:0.1cm;">Discount {{ i["Discount%"]}}%</td>
                               <td id="td" style="text-align:right;padding-right:0.1cm;">{{ i["Discount"] | currency }}</td>
                           </tr>
                           <tr style="font-size:10pt">
                               <td id="td" style="padding-left:0.1cm;">Jumlah Bersih</td>
                               <td id="td" style="text-align:right;padding-right:0.1cm;">{{ i["Jumlah Bersih"] | currency }}</td>
                           </tr>
                        </table>
                        <table>
                            <tr style="font-size:10pt">
                                <td style="text-align:center; width:5cm;margin-left:1cm;">Diterima Dengan Baik <br> Oleh <br><br><br> ________________________</td>
                                <td style="width:10cm;text-align:center"> <i>{{ i["Catatan"] }}</i> </td>
                            </tr>
                        </table>
                    </div>
                </template>
            </div>
        </template>
    </div>
</template>
<script>
    export default { 
        data() {
            return {
                karton: [],
                kartond: [],
            }
        },
        methods: {
            async getData() {
                var uri = this.$apiUrl + 'hpesanps/cetaknotapolos'
                var x = await this.$http.get(uri)
                    this.karton = x.data
                
                    var uri = this.$apiUrl + 'hpesanps/cetaknotapolos1/' + x.data[0]["No Nota"] 
                    var z = await this.$http.get(uri)
                    this.kartond = z.data
            },

            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style scoped>
    @media print {
        #print {
            display: none;
        }

        #template {
            background-color: white
        }
        html, body {
                width: 157mm;
                height: 230mm;
            }

    }
    .grid-container {
            /* margin-top: 0.651cm; */
            margin-top: 0.3cm;
            margin-left: 0.723cm;
            margin-bottom: 0.2cm;
            margin-right: 0.499cm;
            width: 20cm;
        }
        .qr{
            width: 0.7cm; height: 0.7cm;  margin-left: 1.899cm;
            margin-top: 0.051cm;
            -webkit-print-color-adjust: exact; 
        }
        .barcode{
            width: 0.651cm; height: 2.7cm; margin-top: 0.249cm;
            -webkit-print-color-adjust: exact; 
        }
        #tabel{
        border-collapse: collapse;
    }
    #tabel #th, #td {
        border: 1px solid black;
    }
</style>